<script>

export default {
  data() {
    return {
      formData: {
        first_name: "",
        last_name: "",
        email: "",
        password: "",
        user_type: "buyer",
        country: 'United Kingdom',
        phonenumber: '',
        postal_code: '',
        address_line1: '',
        address_line2: '',
        town_city: '',
        county: '',
        delivery_instruction: '',
        securitycode: '',
      },
      
      errors: {},
      loading: false
    };
  },
  methods: {
    userRegister() {
      if(this.formData.phonenumber !='' && this.formData.postal_code !='' && this.formData.address_line1 !='' && this.formData.town_city !=''){
      this.loading = true;
      this.$http
      .post('https://rejeeltd.com/api/register', this.formData).then((response) =>{
          console.log(response.data);
          this.formData.first_name = this.formData.last_name = this.formData.email = this.formData.password = ''
          this.errors = {}
          this.$router.push('/login');
      }).catch(error => {
        if (!error.response) {
            // network error
            this.errorStatus = 'Error: Network Error';
        } else {
            this.errorStatus = error.response.data.message;
            console.log(error.response.data.message);
        }
      }).finally(() => this.loading = false);
      }else{
       this.$refs.err2.style.display = 'block';
      }
    },
    goNext(){
      if (this.formData.first_name !='' && this.formData.last_name !='' && this.formData.email !='' && this.formData.password !=''){
      this.$refs.form1.style.display = 'none';
      this.$refs.form2.style.display = 'block';
    }else{
       this.$refs.err1.style.display = 'block';
    }
   },
   goPrev(){
      this.$refs.form1.style.display = 'block';
      this.$refs.form2.style.display = 'none';
      this.$refs.err1.style.display = 'none';
   },
  }
};
</script>